<template>
  <div class="container min-h-screen pb-10 m-2 md:mx-auto lg:w-1/2">
    <div
      class="p-4 my-5 rounded shadow-md  dark:bg-gray-900 dark:text-white bg-opacity-60"
    >
      <h1 class="my-2 text-2xl font-bold text-center black--text">
        Schedule a task
      </h1>
      <form @submit.prevent="scheduleTask" ref="taskform" class="">
        <label class="inline-block my-2">
          Title <span class="text-red-500">*</span>
        </label>
        <textarea class="form-control" outlined name="task" required></textarea>
        <label class="inline-block my-2"> Target Time(Optional) </label>
        <input name="target" type="datetime-local" class="form-control" />
        <div class="mt-5 text-center">
          <button type="submit" class="primary-btn">Schedule</button>
        </div>
      </form>
    </div>

    <div class="mt-8" v-if="tasks.length > 0">
      <div class="grid grid-cols-1 gap-3 md:grid-cols-2">
        <div>
          <h2 class="mb-5 text-2xl font-semibold text-center dark:text-white">
            Pending Tasks
          </h2>
          <div v-if="rtask.length > 0" class="p-3 bg-gray-200 rounded shadow">
            <transition-group name="fade">
              <template v-for="(task, i) in rtask">
                <div
                  :key="`${i}-hi`"
                  v-if="!task.isComplete"
                  class="flex items-center justify-between px-2 py-5 my-2 bg-white rounded shadow-lg "
                >
                  <div>
                    <h3
                      class="my-2 text-2xl font-bold"
                      :class="{ 'line-through': task.isComplete }"
                    >
                      {{ task.task }}
                    </h3>
                    <p v-if="task.target">
                      {{ new Date(task.target).toLocaleString() }}
                    </p>
                  </div>
                  <div class="flex gap-3">
                    <button
                      v-if="!task.isComplete"
                      class="flex items-center justify-center w-10 h-10 text-white transition-none duration-150 transform rounded-full shadow-md  bg-gradient-to-br from-purple-500 to-indigo-600 hover:shadow-lg hover:scale-105"
                      @click="completeTask(task.id)"
                    >
                      <span class="material-icons"> task_alt </span>
                    </button>
                    <button
                      class="flex items-center justify-center w-10 h-10 text-white transition-none duration-150 transform rounded-full shadow-md  bg-gradient-to-br from-red-500 to-red-600 hover:shadow-lg hover:scale-105"
                      @click="deleteTask(task.id)"
                    >
                      <span class="material-icons"> delete </span>
                    </button>
                  </div>
                </div></template
              >
            </transition-group>
          </div>
          <div
            v-else
            class="flex items-center justify-center h-full border border-gray-200 rounded "
          >
            <p class="text-xl dark:text-white">You have no task scheduled</p>
          </div>
        </div>
        <div :class="{ 'mt-24 md:mt-0': rtask.length == 0 }">
          <h2 class="mb-5 text-2xl font-semibold text-center dark:text-white">
            Completed Tasks
          </h2>
          <div v-if="ctask.length > 0" class="p-3 bg-green-200">
            <transition-group name="fade">
              <template v-for="(task, i) in ctask" :key="`${i}-hi`">
                <div
                  class="flex items-center justify-between px-2 py-5 my-3 bg-white rounded "
                >
                  <div>
                    <h3
                      class="my-2 text-2xl font-bold"
                      :class="{ 'line-through': task.isComplete }"
                    >
                      {{ task.task }}
                    </h3>
                    <p v-if="task.target">{{ timeFormat(task.target) }}</p>
                  </div>
                  <div class="flex gap-3">
                    <button
                      v-if="!task.isComplete"
                      class="flex items-center justify-center w-10 h-10 text-white transition-none duration-150 transform rounded-full shadow-md  bg-gradient-to-br from-purple-500 to-indigo-600 hover:shadow-lg hover:scale-105"
                      @click="completeTask(task.id)"
                    >
                      <span class="material-icons"> edit </span>
                    </button>
                    <button
                      class="flex items-center justify-center w-10 h-10 text-white transition-none duration-150 transform rounded-full shadow-md  bg-gradient-to-br from-red-500 to-red-600 hover:shadow-lg hover:scale-105"
                      @click="deleteTask(task.id)"
                    >
                      <span class="material-icons"> delete </span>
                    </button>
                  </div>
                </div></template
              >
            </transition-group>
          </div>
          <div
            v-else
            class="flex items-center justify-center py-10 bg-green-200 rounded"
          >
            <p class="text-xl">You have no completed task</p>
          </div>
        </div>
      </div>
    </div>
    <h2 v-else class="text-center">No task scheduled</h2>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      tasks: [],
    };
  },

  methods: {
    timeFormat(d) {
      return moment(d).fromNow();
    },
    scheduleTask(e) {
      if (this.$refs.taskform.checkValidity()) {
        let task = Object.fromEntries(new FormData(e.target));
        this.tasks.unshift({
          ...task,
          id: this.tasks.length,
          isComplete: false,
        });
        localStorage.setItem("tasks", JSON.stringify(this.tasks));
        this.$refs.taskform.reset();
      }
    },
    completeTask(id) {
      let idx = this.tasks.findIndex((d) => d.id == id);
      this.tasks[idx].isComplete = true;
      localStorage.setItem("tasks", JSON.stringify(this.tasks));
    },
    deleteTask(id) {
      let idx = this.tasks.findIndex((d) => d.id == id);

      this.tasks.splice(idx, 1);
      localStorage.setItem("tasks", JSON.stringify(this.tasks));
    },
    inittasks() {
      let tasks = localStorage.getItem("tasks");
      if (tasks) {
        this.tasks = JSON.parse(tasks).sort((a, b) => b.id - a.id);
      }
    },
  },
  created() {
    this.inittasks();
  },
  computed: {
    ctask() {
      return this.tasks.filter((t) => t.isComplete);
    },
    rtask() {
      return this.tasks.filter((t) => !t.isComplete);
    },
  },
};
</script>

<style lang="scss" scoped>
</style>